import React, { Component, Suspense } from 'react';
import { toast } from "react-toastify";
import { Accordion, ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import PerfectScrollbar from 'react-perfect-scrollbar'
import ModernWaterfallTemplate from './Components/ModernWaterfallTemplate';
import ModernSlideshowTemplate from './Components/ModernSlideshowTemplate';
import AtmosphereTemplate from './Components/AtmosphereTemplate';

import { getThemesList, createTemplate } from '../../../../Services/Actions/templateAction'

const ModernCardTemplate = React.lazy(() => import('./Components/ModernCardTemplate'));
const SquarePhotoTemplate = React.lazy(() => import('./Components/SquarePhotoTemplate'));
const HorizontalColumnTemplate = React.lazy(() => import('./Components/HorizontalColumnTemplate'));
const GalleryPhotoTemplate = React.lazy(() => import('./Components/GalleryPhotoTemplate'));
const ColorPicker = React.lazy(() => import('../../CommonComponents/ColorPicker'));


class TemplateAside extends Component {
    state = {
        templateName: "",
        selectedTheme: {},
        selectedThemeData: null,
        templateFormData: null,
        currentTheme : 4,
    }
    onChooseTheme = (theme, selectedThemeData) => event => {
        this.setState({ selectedTheme: theme, selectedThemeData: selectedThemeData })
        //console.log('selectedThemeData', selectedThemeData.themeId)
        this.props.changeThemeFunCallBack(selectedThemeData.themeId)

    }
    onTextTemplateName = event => {
        this.setState({ templateName: event.target.value })
    }
    tempFormData = (data) => {
        this.setState({ templateFormData: data })
    }
    onClickSaveChanges = event => {
        const { templateFormData, templateName, selectedThemeData } = this.state
        if (templateFormData && Object.keys(templateFormData).length > 0) {
            var self = this
            const data = {
                "action": 1,
                "name": templateName,
                "theme": selectedThemeData.themeId,
                "cssStatus": templateFormData.customCss.cssStatus ? 1 : 0,
                "css": templateFormData.customCss.css,
                "backgroundColor": templateFormData.background.color,
                "transparent": templateFormData.background.transparent ? 1 : 0,
                "backgroundImage": templateFormData.background.image ? templateFormData.background.image : "",
                "backgroundImageId": 0,
                "link_font": "Homenaje",
                "css_font": "Homenaje",
                "font_varient": "regular",
                "fontColor": templateFormData.cardStyle.textColor,
                "cardColor": templateFormData.cardStyle.cardColor,
                "postText": 0,
                "postAuthor": templateFormData.advanced.author ? 1 : 0,
                "postTime": templateFormData.advanced.time ? 1 : 0,
                "postFeatured": templateFormData.themeSettings.postPopUp ? 1 : 0,
                "minimumPostWidth": 300,
                "padding": templateFormData.themeSettings.postPadding,
                "scrollDelay": 10,
                "slideDelay": 10,
                "numberOfPosts": templateFormData.themeSettings.numberOfPosts,
                "trimcontent": templateFormData.themeSettings.trimContent ? 1 : 0,
                "playFullVideo": 1,
                "randomizePost": templateFormData.themeSettings.suffleAssets ? 1 : 0,
                "hideUrls": 1,
                "fitRow": templateFormData.themeSettings.bestFit ? 1 : 0,
                "roundEdge": 5,
                "cardType": 1,
                "socialAction": 1,
                "timeForPopup": 10,
                "authorColor": templateFormData.cardStyle.authorColor,
                "iconType": 1,
                "iconColor": templateFormData.cardStyle.socialIconColor,
                "fontSize": parseInt(templateFormData.cardStyle.fontSize),
                "shareOption": 1,
                "hideContent": templateFormData.advanced.hideContent ? 1 : 0,
                "numberOfCoumn": templateFormData.themeSettings.columns && Object.keys(templateFormData.themeSettings.columns).length > 0 ? templateFormData.themeSettings.columns.value : 0,
                "slideDuration": 0,
                "slidePost": 0,
                "autoPlay": 0,
                "popupSlideShow": 0,
                "customUrl": "",
                "showHideOldPost": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "showHideOlderPost": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "loadMoreStatus": templateFormData.themeSettings.showmore ? 1 : 0,
                "autoScrollStatus": 0,
                "hidePosts": 0,
                "onsite_settings": "{}"
            }
            createTemplate(data).then((response) => {
                toast.success("New template has been successfully created.")
                // for edit template
                //toast.success("Your changes have been saved successfully")
                self.props.history("/template")
            })
        }
        else {
            toast.error("Please check template data / template name!")
        }
    }

    themeView = (themeId) => {
        console.log('themeId', themeId)
        switch (themeId) {
            case 20:
                return <svg xmlns="http://www.w3.org/2000/svg" width="61.107" height="61.044" viewBox="0 0 61.107 61.044">
                        <rect width="61" height="61" transform="translate(0.107 0.044)" fill="none"/>
                        <path d="M-917.7-441.956v-31.006h18.8v31.006Zm-22.719,0v-41.665h21.936v41.665Zm-19.586,0v-31.006h18.8v31.006Zm42.305-31.976V-503h18.8v29.068Zm-42.305,0V-503h18.8v29.068Zm19.586-10.658V-503h21.936v18.41Z" transform="translate(960 503)" fill="currentColor"/>
                    </svg>
                break;
            case 4:
                return <svg xmlns="http://www.w3.org/2000/svg" width="69" height="69" viewBox="0 0 69 69">
                        <g id="Group_10688" data-name="Group 10688" transform="translate(1233 18010)">
                        <rect id="Rectangle_3861" data-name="Rectangle 3861" width="69" height="69" transform="translate(-1233 -18010)" fill="none"/>
                        <g id="Group_10679" data-name="Group 10679" transform="translate(-31 -51)">
                            <path id="Exclusion_1" data-name="Exclusion 1" d="M140,268H118V247h22v21Zm-23,0H96V247h21v21Zm-22,0H73V247H95v21Zm45-22H118V225h22v21Zm-23,0H96V225h21v21Zm-22,0H73V225H95v21Zm45-22H118V203h22v21Zm-23,0H96V203h21v21Zm-22,0H73V203H95v21Z" transform="translate(-1274 -18160)"  fill="currentColor"/>
                        </g>
                        </g>
                    </svg>              
                break;
            case 47:
                return <svg xmlns="http://www.w3.org/2000/svg" width="69" height="69" viewBox="0 0 69 69">
                            <g id="Group_10690" data-name="Group 10690" transform="translate(1073 18010)">
                            <rect id="Rectangle_3863" data-name="Rectangle 3863" width="69" height="69" transform="translate(-1073 -18010)" fill="none"/>
                            <g id="Group_10683" data-name="Group 10683" transform="translate(57 -103)">
                                <path id="Path_14365" data-name="Path 14365" d="M28.006,282h-19V255h19v27Zm20-2h-19V255h19v25Zm-40,0h-19V255h19v25Zm42.5-11a1.5,1.5,0,1,1,1.5-1.5A1.5,1.5,0,0,1,50.5,269Zm-.226-2.2a.123.123,0,0,0-.079.032.105.105,0,0,0-.031.077.107.107,0,0,0,.031.077l.582.584-.582.578a.112.112,0,0,0-.023.123.115.115,0,0,0,.1.068.1.1,0,0,0,.077-.037l.66-.657a.115.115,0,0,0,0-.158l-.66-.656A.113.113,0,0,0,50.278,266.807ZM-13.489,269A1.511,1.511,0,0,1-15,267.493,1.5,1.5,0,0,1-13.489,266,1.494,1.494,0,0,1-12,267.493,1.5,1.5,0,0,1-13.489,269Zm.22-2.188a.11.11,0,0,0-.075.023l-.655.657a.137.137,0,0,0,0,.164l.655.656a.177.177,0,0,0,.072.039.129.129,0,0,0,.11-.077.151.151,0,0,0-.018-.125l-.582-.563.582-.586a.139.139,0,0,0,0-.165A.177.177,0,0,0-13.269,266.812Z" transform="translate(-1114.006 -18141)" fill="currentColor"/>
                            </g>
                            </g>
                        </svg>              
                break;
            case 50:
                return <svg xmlns="http://www.w3.org/2000/svg" width="69" height="69" viewBox="0 0 69 69">
                            <g id="Group_10691" data-name="Group 10691" transform="translate(991 18010)">
                            <rect id="Rectangle_3864" data-name="Rectangle 3864" width="69" height="69" transform="translate(-991 -18010)" fill="none"/>
                            <g id="Group_10684" data-name="Group 10684" transform="translate(211 -49)">
                                <path id="Path_14366" data-name="Path 14366" d="M-125,262h-44V201h44v61Zm23,0h-22V232h22v30Zm0-31h-22V201h22v30Z" transform="translate(-1032 -18158)" fill="currentColor"/>
                            </g>
                            </g>
                        </svg>              
                break;
            case 58:
                return <svg xmlns="http://www.w3.org/2000/svg" width="61.107" height="61.044" viewBox="0 0 61.107 61.044">
                        <rect width="61" height="61" transform="translate(0.107 0.044)" fill="none"/>
                        <path d="M-917.7-441.956v-31.006h18.8v31.006Zm-22.719,0v-41.665h21.936v41.665Zm-19.586,0v-31.006h18.8v31.006Zm42.305-31.976V-503h18.8v29.068Zm-42.305,0V-503h18.8v29.068Zm19.586-10.658V-503h21.936v18.41Z" transform="translate(960 503)" fill="currentColor"/>
                        </svg>
                break;
            case 21:
                return <svg xmlns="http://www.w3.org/2000/svg" width="69" height="69" viewBox="0 0 69 69">
                            <g id="Group_10693" data-name="Group 10693" transform="translate(825 18010)">
                            <rect id="Rectangle_3866" data-name="Rectangle 3866" width="69" height="69" transform="translate(-825 -18010)" fill="none"/>
                            <g id="Group_10686" data-name="Group 10686" transform="translate(377 -49)">
                                <path id="Path_14368" data-name="Path 14368" d="M-268,262h-33V201h33v61Zm-34,0h-33V201h33v61Z" transform="translate(-866 -18158)"  fill="currentColor"/>
                            </g>
                            </g>
                        </svg>              
                break;
            case 44:
                return <svg xmlns="http://www.w3.org/2000/svg" width="69" height="69" viewBox="0 0 69 69">
                            <g id="Group_10694" data-name="Group 10694" transform="translate(731 18010)">
                            <rect id="Rectangle_3867" data-name="Rectangle 3867" width="69" height="69" transform="translate(-731 -18010)" fill="none"/>
                            <g id="Group_10687" data-name="Group 10687" transform="translate(471 -51)">
                                <path id="Path_14369" data-name="Path 14369" d="M-362,268h-22V247h22v21Zm-23,0h-21V247h21v21Zm-22,0h-22V247h22v21Zm45-22h-22V225h22v21Zm-23,0h-21V225h21v21Zm-22,0h-22V225h22v21Zm45-22h-22V203h22v21Zm-23,0h-21V203h21v21Zm-22,0h-22V203h22v21Z" transform="translate(-772 -18160)" fill="currentColor"/>
                            </g>
                            </g>
                        </svg>              
                break;
        
            default:
                break;
        }
    }
    render() {

        const { themeList, fonts } = this.props
        const { selectedTheme, selectedThemeData, templateName, templateFormData, currentTheme } = this.state
        console.log("templateFormData", this.props)
        return <div className="innerAside flex-shrink-0">
            <div className="card gutter-b">
                <div className="card-body p-0">
                    <PerfectScrollbar>
                        <div className="innerAsideWrapper innerAsideWithAction p-3">
                            <div className="pb-3">
                                <label className="text-gray-800 mb-2">Template Name <strong className="text-danger">*</strong></label>
                                <div className="">
                                    <input type="text" name="templateName" className="form-control form-control-sm is-invalid" placeholder="Enter Template name..." onChange={this.onTextTemplateName} value={templateName} />
                                    <div className="invalid-feedback">Please provide template name</div>
                                </div>
                            </div>
                            <Accordion defaultActiveKey="1">
                                <Accordion.Item eventKey="1" className="pb-3">
                                    <Accordion.Header className="w-100">
                                        <span className="fs-7 fw-semibold">{selectedTheme && selectedTheme.name ? selectedTheme.name : `Choose Theme`}</span>
                                    </Accordion.Header>
                                    <Accordion.Body className="border rounded-1 mt-1">
                                        <ToggleButtonGroup type="radio" className="flex-column w-100" name="themeName" defaultValue={4}>
                                        {themeList.map((item) => {
                                            return <ToggleButton id={`theme-${item.themeId}`} className={`p-0 card rounded-1 text-decoration-none cursor-pointer mb-2`} 
                                            onClick={this.onChooseTheme({ name: item.name, themeId: item.themeId }, item)} value={item.themeId}>
                                                <div className="card-body p-2">
                                                    <div className="d-flex align-items-center">
                                                        <div className="symbol symbol-40 flex-shrink-0 me-2 text-gray-400">
                                                            {this.themeView(item.themeId)}
                                                        </div>
                                                        <div className="overflow-hidden">
                                                            <p className="fs-8 fw-semibold mb-1 text-gray-dark text-ellipsis text-start">{item.name}</p>
                                                            <p className="fs-9 fw-semibold mb-0 text-gray-600 text-ellipsis text-start">{item.product_type == 1 ? `Widget` : item.product_type == 2 ? `Display` : ``}</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </ToggleButton>
                                            })
                                        }
                                        </ToggleButtonGroup>
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion>
                            {
                                selectedTheme ? <>{selectedTheme.themeId == 4 ? <Suspense fallback={<></>}>
                                    <SquarePhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 21 ? <Suspense fallback={<></>}>
                                    <ModernSlideshowTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 20 ? <Suspense fallback={<></>}>
                                    <ModernCardTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 44 ? <Suspense fallback={<></>}>
                                    <AtmosphereTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 50 ? <Suspense fallback={<></>}>
                                    <GalleryPhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 47 ? <Suspense fallback={<></>}>
                                    <HorizontalColumnTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 58 ? <Suspense fallback={<></>}>
                                    <ModernWaterfallTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : null}</> : null
                            }
                        </div>
                    </PerfectScrollbar>
                    <div className="bg-white pt-2 w-100 p-3 rounded-2">
                        <button type="button" className="btn btn-primary w-100" onClick={selectedTheme ? this.onClickSaveChanges : null}>Save Changes</button>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default TemplateAside;